                prematureOrExpiredTime: '', //早产或过期 周数
                selectPrinterVisible: false,
                printerOptions: [{
                    value: 'print',
                    label: '广东省智力残疾儿童康复档案'
                }, {
                    value: 'autismPrinter',
                    label: '广东省孤独症儿童康复档案'
