                    if (_this.userInfo.Member3 == null) {
                        _this.userInfo.Member3 = {
                            ID: "",
                            Name: "",
                            Age: '',
                            Degree: '',
                            WorkUnit: '',
                            Title: '',
                            Mobile: ''
                        };
                    }
                    //早产或过期分成2个字段
                    let length = _this.userInfo.PrematureOrExpired == null ? 0 : _this.userInfo.PrematureOrExpired
                        .length;

                    if (length > 0) {
                        _this.prematureOrExpiredText = _this.userInfo.PrematureOrExpired.substr(0, 2);
                        _this.prematureOrExpiredTime = _this.userInfo.PrematureOrExpired.substr(2, length);
                    }
                    if (_this.userInfo.ThreatenedAbortion) {
                        _this.abortionStatus = 'true';
                    }
                    if (_this.userInfo.Mature) {
                        _this.matureStatus = 'true';
                    }
                    if (_this.userInfo.IllnessHistory) {
                        _this.illnessHistoryStatus = 'true';
                    }
                    if (_this.userInfo.AllergyHistory) {
                        _this.allergyHistoryStatus = 'true';
                    }
                    if (_this.userInfo.SpecialEatingHabits) {
                        _this.SpecialEatingHabitsStatus = 'true';
                    }
                    if (_this.userInfo.SpecialSleepingTime) {
                        _this.SpecialSleepingTimeStatus = 'true';
                    }
                    _this.requestDiagnosises();
                }).catch(err => {
                    _this.$message.error('获取患者信息失败！');
                });

                //获取家庭模式(FAMILYMODEL)、居住社区(COMMUNITY)、教养方式(EDUCATIONSTYLE)、语言环境(LANGUAGE)、抚养带教人(DEPENDENTS)

                api.getDictionaryData('FAMILYMODEL').then(res => {
                    _this.familyModels = res.data.List;
                }).catch(err => {

                });

                api.getDictionaryData('COMMUNITY').then(res => {
                    _this.communities = res.data.List;
                }).catch(err => {

                });

                api.getDictionaryData('EDUCATIONSTYLE').then(res => {
                    _this.educationStyles = res.data.List;
                }).catch(err => {

                });

                api.getDictionaryData('LANGUAGE').then(res => {
                    _this.languages = res.data.List;
                }).catch(err => {

                });

                api.getDictionaryData('DEPENDENTS').then(res => {
                    _this.dependents = res.data.List;
                }).catch(err => {

                });

            },
            //请求病种数据
            requestDiagnosises() {
                let _this = this;
                api.requestDept().then(res => {
                    _this.diagnosisesDept = res.data.List;
                    let flag = false;
                    for (let i = 0; i < _this.diagnosisesDept.length; i++) {
                        for (let j = 0; j < _this.diagnosisesDept[i].Diagnosises.length; j++) {
                            if (_this.diagnosisesDept[i].Diagnosises[j].ID == _this.userInfo.DiagnosisID) {
                                let id = _this.diagnosisesDept[i].ID;
                                let name = _this.diagnosisesDept[i].Name;
                                _this.$store.dispatch('setCurrentPatientDept', {
                                    ID: id,
                                    DeptName: name,
                                    Name: _this.userInfo.Name,
                                    Sex: _this.userInfo.Sex,
                                    BirthDate: _this.userInfo.BirthDate
                                })
                                _this.currentDiaDeptId = _this.diagnosisesDept[i].ID;
                                _this.diagnosises = _this.diagnosisesDept[i].Diagnosises
                                flag = true;
                                break;
                            }
                        }
                        if (flag)
                            true;
                    }
                }).catch(err => {
                    _this.$message.error('病种数据请求失败,请稍后再试');
                });
            },

            //科室发生变化时，病种下拉列表刷新
            deptOnChange(id) {
                let _this = this;
                _this.diagnosises = [];
                for (let i = 0; i < _this.diagnosisesDept.length; i++) {
                    if (_this.diagnosisesDept[i].ID === id) {
                        _this.diagnosises = _this.diagnosisesDept[i].Diagnosises;
                        if (_this.diagnosises.length > 0) {
                            _this.userInfo.DiagnosisID = _this.diagnosises[0].ID;
                        } else {
                            _this.userInfo.DiagnosisID = '';
                        }
                        break;
                    }
                }
            },
            //更新患者信息
            updateUserinfo() {
                let _this = this;
                console.log(_this.userInfo);
                let params = _this.handleSpecialFields(_this.userInfo);
                api.postPatientAgentDetails(params).then(res => {
                    if (res.data.Code === 0) {
                        _this.$message.success('保存成功！');
                        _this.refreshPatientInfo();
                    }
                }).catch(err => {
                    _this.$message.error('保存失败,请稍后再试！');
                });
            },
            printPreview() {
                this.selectPrinterVisible = true;
            },
            jumpToPrinter() {
                if (this.printValue === '') {
                    this.$message.warning('请选择打印档案类型');
                } else {
                    let path = '/' + this.printValue;
                    let pIndex =  this.printerOptions.map(ele => ele.value).indexOf(this.printValue);
                    let str = this.printerOptions.map(ele => ele.label)[pIndex];
                    let pTitle =  _.trim(str, '广东省康复档案');
                    console.log('pTitle', pTitle);
                    this.$store.dispatch('setPrintPaperTitle', pTitle);
                    
                    this.$router.push({
                        path: path
                    });
                }
            },
            handleSpecialFields(userInfo) {
                if (this.abortionStatus === 'false') {
                    this.userInfo.ThreatenedAbortion = '' //先兆流产
                }
                if (this.matureStatus === 'true') { //足月
                    this.userInfo.Mature = ''
                }
                if (this.illnessHistoryStatus === 'false') { //有无病史
                    this.userInfo.IllnessHistory = ''
                }
                if (this.allergyHistoryStatus === 'false') { //过敏史
                    this.userInfo.AllergyHistory = ''
                }
                if (this.SpecialSleepingTimeStatus === 'false') { //特殊睡眠
                    this.userInfo.SpecialSleepingTime = ''
                }
                if (this.SpecialEatingHabitsStatus === 'false') { //特殊饮食
                    this.userInfo.SpecialEatingHabits = ''
                }
                this.userInfo.PrematureOrExpired = this.prematureOrExpiredText + this.prematureOrExpiredTime
                return this.userInfo;
            },
            refreshPatientInfo() {
                api.getDoctorPatientList().then(res => {
                    let arr = [];
                    var groupInfo = res.data.List;
                    for (var i = 0, j = groupInfo.length; i < j; i++) {
                        var obj = {
                            'groupName': groupInfo[i].Name,
                            'groupID': groupInfo[i].ID,
                            'groupPatients': groupInfo[i].Agents
                        };
                        arr.push(obj);
                    }
                    this.$store.dispatch('addPatientList', arr);
                })
            },
            getAuth() {
                return {
                    'authorization': localStorage.getItem('accessToken')
                };
            },
            getuploadURL() {
                return api.uploadIMGURL;
            },
            handleAvatarScucess(res, file) {
                if (res.Code === 0) {
                    this.imageUrl = res.Path;
                    this.userInfo.Logo = res.Msg;
                } else {
                    this.$message.error('上传照片失败，请重试');
                }
            },
            beforeAvatarUpload() {},
            changetags(currentValue) {
                console.log('现在的值');
                console.log(currentValue);
            },
            change1(text) {
                this.userInfo.Dependents = text;
            },
            change2(text) {
                this.userInfo.Language = text;
            },
            change3(text) {
                this.userInfo.EducationStyle = text;
            },
            change4(text) {
                this.userInfo.Community = text;
            },
            change5(text) {
                this.userInfo.FamilyModel = text;
            }
        },
        created() {
            if (window.innerHeight < 768) {
                this.narrow = true;
            } else {
                this.narrow = false;
            }
            this.init();
        },
        mounted() {
            window.onresize = () => {
                return (() => {
                    if (window.innerHeight < 768) {
                        this.narrow = true;
                    } else {
                        this.narrow = false;
                    }
                })()
            }
        }
    };

</script>


